import React from 'react'
import styled from 'styled-components'
import { TextField, Button } from '@mui/material'
import { useState } from 'react'
import { useNavigate } from 'react-router-dom'
import Auth from '../../hoc/auth'
import { useDispatch } from 'react-redux'
import { registerUser } from '../../_actions/user_action'
import { checkUsername } from '../../_actions/profile_action'

const PageWrap = styled.div`
  	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
    padding-top: 70px;
`

const LoginWrap = styled.div`
	display: flex;
	flex-direction: column;
    margin-top: 20px;
    width: 400px;
    height: 300px;
`

const IdWrap = styled.div`
	display: flex;
	align-items: center;
`

function RegisterPage() {
	const navigate = useNavigate()
	const dispatch = useDispatch()

	const [username, setUsername] = useState('')
	const [password, setPassword] = useState('')
	const [confirmPassword, setConfirmPassword] = useState('')
	const [redundancyChecked, setRedundancyChecked] = useState(false)

	const nameChanged = (e) => {
		setUsername(e.target.value)
	}

	const passwordChanged = (e) => {
		setPassword(e.target.value)
	}

	const confirmPasswordChanged = (e) => {
		setConfirmPassword(e.target.value)
	}

	const registerHandler = async () => {
		if (!redundancyChecked) {
			return alert('  .')
		}
		if (password !== confirmPassword) {
			return alert('    .')
		}
		const regex = /(?=.*[0-9])(?=.*[a-zA-Z])(?=.*\W)(?=\S+$).{8,20}/
		if (!regex.test(password)) {
			return alert('  , ,   1   8 ~ 20  .')
		}
		

		let body = {
			username,
			password
		}
		try {
			await dispatch(registerUser(body))
			alert(' .  .')
			navigate('/initProfile')
		} catch (e) {
			alert(e.response.data.message)
		}
	}

	const redundancyChecker = async () => {
		let body = {
			username
		}
		try {
			const res = await dispatch(checkUsername(body))
			if (res.payload.isExist) {
				setRedundancyChecked(false)
				alert('  .')
			}
			else {
				setRedundancyChecked(true)
				alert(' .')
			}
		} catch (e) {
			console.log(e)
		}
	}

	return (
		<PageWrap>
			<h1></h1>
			<LoginWrap>
				<IdWrap>
					<TextField fullWidth margin="dense" label="" variant="outlined" size="small" onChange={nameChanged} />
					<Button variant="outlined" sx={{ ml: 2, fontSize: 10, height: 40}} size="small" onClick={redundancyChecker} > </Button>
				</IdWrap>
				<TextField fullWidth='100px' type='password' margin="dense" label="" variant="outlined" size="small" onChange={passwordChanged} />
				<TextField fullWidth type='password' margin="dense" label=" " variant="outlined" size="small" onChange={confirmPasswordChanged} />
				<Button fullWidth margin="dense" variant="contained" sx={{ my: 2 }} onClick={registerHandler} >register</Button>
			</LoginWrap>
		</PageWrap>
	)
}

export default Auth(RegisterPage, false)